const Cycle = require('@cycle/core');
const CycleDOM = require('@cycle/dom');
const Rx = require('rx');
const u = require('dpaw-brocket-utility');

const log = u.log;
const h = CycleDOM.h;
const Obs = Rx.Observable;
const {
  div, form, fieldset, legend, input, label, button
} = require('hyperscript-helpers')(h);

const INIT_STATE = {
  username: 'gavinco',
  channel: 'd46578fc25bfd5cac1cc49700bd820068e38f339575ec8708f3882b96a4bfb39',
  appID: 'qgis'
};

function AdminDialogue({DOM, incoming$}) {
  let initState$ = Rx.Observable.just(INIT_STATE);
  let actions = intent(DOM);
  let state$ = model(initState$, actions);
  let vtree$ = view(state$);
  return { state$, vtree$ };
}

function intent(DOM) {
  let actions = {
    channel$: DOM.select('#channel')
      .events('change')
      .map( ev => ev.target.value )
  };
  return actions;
};

function model(initState$, actions) {
  let channel$ = actions.channel$;
  channel$.subscribe( chan => log('Admin.model: new channel ' + chan));
  let state$ = Obs.combineLatest(initState$, actions.channel$, merge);
  let init$  = Obs.combineLatest(initState$, merge);
  // return state$;
  log('Admin: initState$ is', initState$);
  log('Admin: channel$ is', channel$);
  log('Admin: init$ is', init$);
  log('Admin: state$ is', state$);
  return init$;
}

function merge(init, chan) {
  let state = {
    ...init
  };
  return state;
}

function view(state$) {
  let vtree$ = state$.map(render);
  log('AdminDialog.view: vtree$', vtree$);
  return vtree$;
}

function render(state) {
  let _view = div('.admin-container', [
    form('.pure-form.pure-form-stacked', [
      fieldset([
        legend('A stacked inline form'),
        textInput({id: 'username', text: 'Username', value: state.username}),
        textInput({id: 'channel', text:'Channel', value: state.channel}),
        textInput({id: 'appID', text:'AppID', value: state.appID}),
      ])
    ])
  ]);
  return _view;
}

function textInput(opts) {
  let {id, text, value} = opts;
  let placeholder = opts.placeholder || text;
  let type = opts.type || '';
  let input = div([
    label({for: id}, text),
    h(`input#${id}.g-input`, {type, placeholder, value}),
  ] );
  return input;
}

AdminDialogue.module = {
  intent, model, view
};

module.exports = AdminDialogue;



