let Rx = require('rx');
let u = require('dpaw-brocket-utility');
let log = u.log;
let Obs = Rx.Observable;


function model(actions, incoming$, outgoing$) {
  let messageArr$ = incoming$
        // .do(msg => log('Model: incoming msg', msg))
        .pluck('payload')
        .scan(myAggregate, [])
        .startWith(['in the beginning']);

  let outgoingArr$ = outgoing$
        // .do(msg => log('Model: outgoing msg', msg))
        .map(msg => [msg])
        .startWith([{tag: 'map', payload: {title: 'mapz are here'}}]);

  let state$ = Obs.combineLatest(
    messageArr$, outgoingArr$,
    (messageArr, outgoingArr) => ({ messageArr, outgoingArr })
  );
  return state$;
}

module.exports = model;

function myAggregate(acc, val, ix, src) {
  acc.push(val);
  return acc.slice(-10);
}



