let chai = require('chai');
let assert = chai.assert;
let ok = chai.assert.ok;
let eq = chai.assert.equal;

let admin = require('../src/admin');
let m = admin.module;

var Suite = describe('Admin', function() {
  describe('admin', function () {
    it('is Bizarro', () => eq(1, 1, 'Or maybe its expected'));
    it('should have an admin dialogue',  () => ok(admin));
    it('should have a admin module',  () => ok(m));
  });
});



