const Rx = require('rx');
const Relay = require('dpaw-brocket-relay');
const u = require('dpaw-brocket-utility');
const log = u.log;
const Obs = Rx.Observable;

function makeRelayDriver(opts) {

  let relay = Object.create(Relay);
  let relayOpts = {
    baseAddr: "wss://brocket.dpaw.wa.gov.au",
    channel: '',
    appID: 'monorail',
    userID: 'gavinco'
  };
  relay.init(relayOpts);
  relay.inject_rx(Rx);
  // relay.on('message', msg => log('RelayDriver received msg', msg));
  relay.on('dispatch', msg => log('RelayDriver: dispatched msg', msg));
  // relay.on('receive', msg => log('RelayDriver received msg', msg));
  // log('RelayDriver.makeRelayDriver: has relay', relay);
  relay.subscribe(
    msg => log('Monorail.plugin: Subscriber received msg ', msg),
    msg => log('Completed: ', msg),
    err => log('Error in relay-driver:', err)
  );
  relay.connect();
  relay.on('pub/open', () => {
    relay.dispatch({tag: 'wrapped', payload: {tag: 'unwrapped', payload: 'kaboom'}});
    log('Pub channel open');
  });

  function relayDriver(state$) {
    // log('RelayDriver.relayDriver: have outgoing$', state$);
    let outgoing$ = state$
          .do(msg => log('RelayDriver.outgoing$: filtering', msg))
          .filter(notLocal)
          .do(msg => log('RelayDriver.outgoing$: past filter', msg));
    let channel$  = state$
          .do(msg => log('RelayDriver.channel$: filtering', msg))
          .filter(isLocal)
          .do(msg => log('RelayDriver.outgoing$: past filter', msg))
          .pluck('channel')
          .distinctUntilChanged();
    outgoing$
      .do(msg => log('RelayDriver.outgoing$: dispatching', msg))
      .subscribe(
      msg => relay.dispatch(msg),
      msg => log('Completed: ', msg),
      err => log('Error in outgoing$:', err)
    );
    channel$
      // .do( chan => log('New channel: ' + chan))
      .subscribe(channel => {
        log('RelayDriver.channel$', channel);
        if (channel === relay.channel()) return;
        else relay.channel(channel);
      }
    );
    let incoming$ = relay.message$  // not working properly
          .filter( v => !u.isUndefined(v) )
          // .filter( msg => msg.tag.search(/beat/) < 0 ) // dont want beat
          .share();
    return incoming$;
  }

  return relayDriver;

}

module.exports = makeRelayDriver;

function isLocal(msg) {
  return u.has(msg, 'channel');
}
var notLocal = u.negate(isLocal);



