let Cycle = require('@cycle/core');
let CycleDOM = require('@cycle/dom');
let Rx = require('rx');
let u = require('dpaw-brocket-utility');

let h = CycleDOM.h;
let Obs = Rx.Observable;
let log = u.log;

function view(state$, incoming$, outgoing$, admin$) {
  let vtree$ = Obs.combineLatest(
    state$, incoming$, outgoing$, admin$,
    render
  );
  return vtree$;
}

function render(state, incoming, outgoing, admin) {
  // log('monorail.view.render: got some new state', state);
  // log('monorail.view.render: got some new admin', admin);
  let view = h('div.app-container', [
    h('h2.clean', 'Welcome to the local monorail plugin'),
    h('div.pure-g', [
      h('div.g-log.g-incoming.pure-u-7-24', [ incoming ]),
      h('div.g-log.g-outgoing.pure-u-7-24', [ outgoing ]),
      h('div.g-log.admin.pure-u-7-24',      [ admin ])
    ])
  ]);
  return view;
}


module.exports = view;


function renderWeightSlider(weight) {
  return h('div', [
    'Weight ' + weight + 'kg',
    h('input#weight', {type: 'range', min: 40, max: 140, value: weight})
  ]);
}

function renderHeightSlider(height) {
  return h('div', [
    'Height ' + height + 'cm',
    h('input#height', {type: 'range', min: 140, max: 210, value: height})
  ]);
}



