let u = require('dpaw-brocket-utility');
const {APP_TAG_SEP} = require('./constants');

u.ignoreApp     = u.curry(ignoreApp);
u.isObservable  = isObservable;
u.isAppMap      = isAppMap;
u.hasSyn        = hasSyn;
u.isAck         = msg => u.has(msg, 'ack');

u.notObservable = u.negate(isObservable);
u.notAppMap     = u.negate(isAppMap);
u.notNull       = u.negate(u.isNull);
u.notEmpty      = u.negate(u.isEmpty);
u.notAck        = u.negate(u.isAck);

module.exports = u;

function ignoreApp(appID, msg) {
  let {tag, payload} = msg;
  let [app, db, op] = tag.split(APP_TAG_SEP);
  return app !== appID;
}

function isObservable(obj) {
  // u.log('isObservable: recv obj', obj);
  return u.isFunction(obj.combineLatest);
}

function isAppMap(obj) {
  u.log('isAppMap: recv obj', obj);
  return u.has(obj, 'base') &&
    u.has(obj, 'layout') &&
    u.has(obj, 'tileLayers') &&
    u.has(obj, 'featureLayers');
}

function hasSyn(msg) {
  return u.has(msg, 'syn');
}



