/**
 * @fileOverview
 * @name command.js
 * @author Gavin Coombes
 * @license BSD-3-Clause
 *
 * Dispatch commands to the QGIS-Monorail server
 */

let axios = require('axios');
let events = require('events');
let Rx = require('rx');
let u = require('./utility');
const {APP_TAG_SEP} = require('./constants');

// Local Aliases
var log = u.log;
const Obs = Rx.Observable;
window.axios = axios;

var CommandModule = (function(){
  let obj = Object.create(new events.EventEmitter());
  obj.$type = "CommandModule";
  return obj;
})();

var CommandObject = Object.create(CommandModule);

CommandObject.init = function init(opts){
  log('Building command object *****************************');
  let self = this;
  self.opts = opts;
  self.type = 'CommandObject';
  self._port = opts.port || 30000;
  self.baseAddress = opts.address || buildAddress(this._port);
  self.fetchAddress = self.baseAddress + 'fetch';
  self.interval = opts.interval || 10 * 1000;
};

CommandObject.port = function port(value) {
  let self = this;
  if (arguments.length) {
    self._port = value;
    self.address = buildAddress(self._port);
    log('CommandObject.port: port now ', self._port);
    return self._port;
  } else {
    return self._port;
  }
};

CommandObject.fetch = function fetch(address) {
  let self = this;
  let promise =  axios.get(self.fetchAddress)
        .then( msg => self.notify(msg.data) )
        .catch( err => console.log("Fail on fetch", err) );
  return promise;

};

CommandObject.command = function command(cmd) {
  let self = this;
  let {tag, payload, cuid} = cmd;
  let params = `${tag}?${payload}`;
  let address = self.baseAddress + params;
  let promise = axios.post(address)
        .then( response => {
          // log('CommandObject.command: response', response);
          let tag = ['qgis', 'map', 'ack'].join(APP_TAG_SEP);
          let ack = {tag, payload: response.data, ack: cuid};
          self.notify(ack);
        })
        .catch(err => console.log(err));
  return promise;
};

CommandObject.run = function run() {
  this.fetch(this.address)
    .then(window.setTimeout(this.run.bind(this), this.interval));
};

CommandObject.notify = function notify(res) {
  // log('Command.notify: emitting', res);
  this.emit('data', res);
};

function buildAddress(port) {
  return `http://localhost.daplie.com:${port}/api/`;
  // return `http://localhost:${port}/api/`;
}

function makeCommand(incoming$, opts) {

  // log('Command.makeCommand: have incoming$', incoming$);
  // log('Command.makeCommand: have opts', opts);
  let c = Object.create(CommandObject);
  let command$ = incoming$
        .do(m => log('Command.makeCommand: prefilter msg is ', m))
        .filter(u.ignoreApp('qgis'))
        .do(m => log('Command.makeCommand: postfilter msg is ', m));

  command$.subscribe(
    c.command.bind(c),
    msg => log('Completed: ', msg),
    err => log('Error in command$:', err)
  );
  c.init(opts);
  let outgoing$ = Obs.fromEvent(c, 'data')
        // .do(msg => log('makeCommand: outgoing msg', msg))
        .share();
  c.run();
  return outgoing$;
}

function ignoreApp(appID, msg) {
  let {tag, payload} = msg;
  let [app, db, op] = tag.split(APP_TAG_SEP);
  return app !== appID;
}


module.exports = makeCommand;



