const Rx = require('rx');
const chai = require('chai');
const u = require('../src/utility');

let log = u.log;

const Obs = Rx.Observable;
const assert = chai.assert;
const expect = chai.expect;
const ok = chai.assert.ok;
const eq = chai.assert.equal;

let note = require('../src/syn-notification-stream');
let m = note.module;

var Suite = describe('Notifications', function() {
  describe('basics', function () {
    it('is not Bizarro', () => eq(1, 1, 'Or maybe its expected'));
    it('should have an notification dialogue',  () => ok(note));
    it('should have a notification module',  () => ok(m));
  });

  describe('types', () => {
    it('should throw a TypeError if not given a notification stream', () => {
      expect(note).to.throw(TypeError);
    });
    it('should return an Observable stream', () => {
      let result = note(Obs.just({}));
      ok(u.isObservable(result));
    });
  });
});



