/**
 * @fileOverview
 * @name transform.js
 * @author Gavin Coombes
 * @license BSD-3-Clause
 *
 * Transform messages from the network -> QGIS ie appwards
 *  and from QGIS -> network ie. netwards
 *
 * Current recognised tags-commands
 *   add_feature
 */

let u = require('dpaw-brocket-utility');
let Rx = require('rx');
const {APP_TAG_SEP} = require('./constants');

let log = u.log;
let Obs = Rx.Observable;


// Map tag to handler
const MAPPING = {
  add_feature,
  transport,
  set_extent,
  querystring: {
    add_feature: add_feature_querystring,
    set_extent:  set_extent_querystring
  }
};

function netwards(mapping, msg) {
  // NB. For use with flatMap so must return a stream or Array
  // At the moment the only messages from qgis are full map;
  log('Transform.netwards: appMap', msg);
  const direction = 'netwards';
  let netmsg;
  if (u.isAck(msg)) {
    log('Transform.netwards: got ack', msg);
    netmsg = msg;
  } else if (u.isAppMap(msg)) {
    log('Transform.netwards');
    let tag = 'qgis:map:transport';
    let [app, db, op] = tag.split(APP_TAG_SEP);
    let func = mapping[op] || u.log;
    netmsg = func(mapping, msg, direction) || null ;
  }
  log('Transform.netwards: netmsg', netmsg);
  return netmsg;
}

function appwards(mapping, msg) {
  // log('Appwards received msg', msg);
  const direction = 'appwards';
  let {tag, payload, ...rest} = msg;
  let [app, db, op] = tag.split(APP_TAG_SEP);
  let func = mapping[op] || u.log;
  let cmdmsg$ = func(mapping, payload, direction, rest) || [ null ];
  return cmdmsg$;
}

/**
 * Convert map between net and app representations
 * @param {object} m - mapping from tags to functions
 * @param {object} _map - an appmap or urmap
 * @param {string} direction - 'appwards | 'netwards'
 * @returns {object} msg$ - an Rx stream or array for use with flatMap
 * @throws {} TypeError for unknown directions
 */
function transport(m, _map, direction, rest={}) {
  log('Transform.transport: _map is ', _map);
  let msg$;
  let cuid = rest.cuid;
  if (direction === 'appwards')      { msg$ = transport_appwards(m, _map, cuid); }
  else if (direction === 'netwards') { msg$ = transport_netwards(_map);}
  else {throw new TypeError(`Unknown direction ${direction}`);}
  return msg$;
};

function transport_appwards(m, _map, cuid) {
  return [m.set_extent(m, _map.base.bounds, cuid),
          m.add_feature(m, _map, _map.layout.layers[0], cuid)];
}

function transport_netwards(_map) {
  return {tag: 'qgis:map:transport', payload: _map};
}

function add_feature(mapping, urmap, feat_id, cuid) {
  u.log('Transform.add_feature: ');
  let ly = urmap.featureLayers[feat_id];
  let p = {
    data_source:   ly.data_source,
    layer_name:    ly.layer_name,
    provider_name: ly.provider || 'ogr',
    data:          JSON.stringify(ly.data)     || ''
  };

  if (p.data_source === '$ref') {
    p.data = JSON.stringify(urmap.data[feat_id]);
    p.data_source = '';
  };

  let cmd = 'add_vector_layer';
  let qs = mapping.querystring.add_feature(p);
  let cmd_msg = {tag: cmd, payload: qs, cuid};
  u.log('Transform.add_feature: cmd_msg is', cmd_msg);
  return cmd_msg;

}

function add_feature_querystring (p) {
  //     `data_source=${p.data_source}&layer_name=${p.layer_name}&provider_name=${p.provider_name}&data=${p.data}`;
  return `data_source=${p.data_source}&layer_name=${p.layer_name}&provider_name=${p.provider_name}&data=${p.data}`;
}

function set_extent_querystring(p) {
    return `xmin=${p.xmin}&ymin=${p.ymin}&xmax=${p.xmax}&ymax=${p.ymax}`;
};

function set_extent(mapping, payload, cuid) {
  // Accept an urBounds payload and xform to QGIS set_extent command
  // set_extent(**{'xmin': 0, 'ymin': 0, 'xmax': 1', 'ymax': 1})
  let {minLon, minLat, maxLon, maxLat} = payload;
  let p = {xmin: minLon, ymin: minLat, xmax: maxLon, ymax: maxLat};
  let cmd = 'set_extent';
  let qs = mapping.querystring.set_extent(p);
  let cmdmsg = {tag: cmd, payload: qs, cuid};
  return cmdmsg;
}


let Transform = {
  appwards: u.partial(appwards, MAPPING),
  netwards: u.partial(netwards, MAPPING)
};

module.exports = Transform;



