'use strict';

const Cycle = require('@cycle/core');
const CycleDOM = require('@cycle/dom');
const Rx = require('rx');
Rx.config.longStackSupport = true;

const u = window.u = require('./utility');

const h = CycleDOM.h;
const makeDOMDriver = CycleDOM.makeDOMDriver;
const makeRelayDriver = require('./relay-driver');
const log = u.log;
const Obs = window.Obs = Rx.Observable;

const intent = require('./intent');
const model = require('./model');
const view = require('./view');
const xform = require('./transform');
const makeCommand = require('./command');
const LogComponent = require('./logs-component');
const AdminDialog = require('./admin');
// const ackSentinel = require('./ack-sentinel');
// const notify = require('./syn-notification-stream');

// UPDATED!
function main({DOM, relay}) {
  let {channel, port} = parseQuery(window.location);
  console.log(`Parsed channel ${channel} and port ${port}`);

  let channel$ = Obs.just({channel});

  // Incoming streams
  let incoming$     = relay.filter(u.ignoreApp('qgis')).share();
  let incomingLog   = LogComponent(DOM, incoming$, {title: 'incoming'});
  let admin         = AdminDialog({DOM, incoming$});

  // The Command Object takes a stream of commands and options and
  // returns any messages returning from qgis.
  let commandOpts = {channelName: channel, port: port};
  let command$ = incoming$
        .flatMap(xform.appwards)
        .filter(u.notNull);
  let outgoing$ = makeCommand(command$, commandOpts)
        .map(xform.netwards)  // TODO: Should really flatmap this for symmetry.
        .do(msg => log('plugin.main.outgoing$', msg))
        .filter(u.notNull)
        .share();
  let outgoingLog   = LogComponent(DOM, outgoing$, {title: 'outgoing'});


  // The relay needs the outgoing messages and any changes to the channel.
  let relay$ = Obs.merge(outgoing$, channel$).share();

  // Main loop
  let action$ = intent(DOM, incoming$);
  let state$  = model(action$, incoming$, outgoing$);
  let newDOM$  = view(state$, incomingLog.vtree$, outgoingLog.vtree$, admin.vtree$);

  return {
    DOM: newDOM$,
    relay: relay$
  };
}

module.exports = main;


function parseQuery(location) {
  log('Parsing the query string');
  let reChannel = /[\\?&]channel=([^&#]*)/;
  let rePort = /[\\?&]port=([^&#]*)/;
  let [_, channel] = reChannel.exec(location.search);
  let [__, port] = rePort.exec(location.search);
  return {channel, port};
}



