/**
 * @fileOverview
 * @name syn-notification-stream.js
 * @author Gavin Coombes
 * @license BSD-3-Clause
 *
 * Transform incoming stream to notification stream.
 *
 * For each syn request we need to set up a ack action with a notification.
 * The notification requires:
 * 1. Action predicate - Is the action triggered?
 * 2. Action thunk - produce the ack
 * 3. Cleanup predicate - Should the action be removed? timeout perhaps.
 *
 * Maybe there should be a cleanup stream

 From the main plugin file.
 // Over-complex ack framework - keep code for arbitrary acks for a while 20151120
 // The ackSentinel requires note$, map$ and produces ack$
 // let notification$ = notify(incoming$);
 // let ack$          = ackSentinel(notification$, outgoing$);
 // notification$.subscribe(msg => log('Notifications: ', msg));

 */

const Rx = require('rx');
const u = require('./utility');

const Obs = Rx.Observable;
let log = u.log;

function notify(incoming$) {
  log('notify: received incoming$:', incoming$);
  // log('notify: incoming$.combineLatest', incoming$.combineLatest);
  // if (u.notObservable) throw new TypeError('incoming$ must have combineLatest method');

  let note$ = incoming$
        .do(msg => log('Notify: saw msg', msg))
        .filter(hasSyn)
        .map(makeNote)
        .do(msg => log('Notify: made note ', msg));
  return note$;

};

/**
 * Make an ack notification.
 * @param {} msg
 * An notification has
 *  - an ack predicate
 *  - an ack thunk
 */
function makeNote(msg) {
  // example tag
  let {syn, cuid, time} = msg;

  // We know that if its a prs map transport then we must see the grom in the mapstate

  let note = {
    syn,
    cuid,
    pred:  makeAckPred(msg),
    ack: makeAck(msg),
    time
  };
  return note;
}

function makeAckPred(msg) {
  let {tag, payload: map} = msg;
  let geom_id = map.layout.layers[0];
  let geom = map.featureLayers[geom_id];
  let geom_name = geom.layer_name;

  let pred = function (msg) {
    // Expect QGIS to use layer name as legend entry
    log('ackPred: recv msg', msg);
    let _map = msg.payload;
    log('pred: Looking at layers in ', _map);
    return u.contains(_map.layout.layers, geom_name);
  };
  return pred;
}

function makeAck(msg) {
  let {cuid} = msg;
  return {ack: cuid};
}


function hasSyn(msg) {
  log('hasSyn: recv msg', msg);
  return u.has(msg, 'syn');
}

notify.module = {
  hasSyn, makeNote
};

module.exports = notify;



